.tf-number input {
  @include field;
}

.tf-number-field {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.tf-number-button-group {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding: 0;
  height: $button-height; // Remove border

  .tf-button-number {
    @include button;
    border-radius: 0;
    // display: flex;
    align-items: center;
    // justify-content: center;
    line-height: 0;
    // padding: $button-padding;
    height: 50%;
    // background: #fff;
    // border: solid 1px #d3d3d3;
    // color: #505050;

    &:first-child {
      border-top-left-radius: $button-border-radius;
      border-top-right-radius: $button-border-radius;
    }
  
    &:last-child {
      border-bottom-right-radius: $button-border-radius;
      border-bottom-left-radius: $button-border-radius;
      border-top: $button-border;
    }

  }
  
}
